nav {
  position: sticky;
  top: 0;
  width: 100%;
  height: var(--navbar-height);
  z-index: 10;
  background: var(--bg-2);
  display: flex;
  align-items: center;
  justify-content: center;
  .content {
    box-sizing: border-box;
    width: min(1200px, 100%);
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;

    @media (max-width: 350px) {
      padding: 0 0.25rem;
    }
    .retro-popcorn {
      height: 80%;
      img {
        height: 100%;
      }
      img:last-child {
        box-sizing: border-box;
        margin-left: -10%;
        padding-bottom: 10%;
        transform: scale(0);
        transition: transform 0.5s ease-in-out;
      }
    }
    .retro-popcorn:hover {
      img:last-child {
        transform: scale(1);
      }
    }
    #home-btn {
      border: none;
      background: transparent;
      height: 100%;
      display: flex;
      align-items: center;
      gap: 0.5rem;

      .logo {
        height: calc(0.5 * var(--navbar-height));
        width: auto;
      }
    }

    #home-btn:hover {
      cursor: pointer;
    }
  }
  h1 {
    font-family: 'Rowdies', sans-serif;
    font-weight: 500;
    font-style: normal;
    text-align: left;
    text-transform: uppercase;
    font-size: clamp(0.8rem, 5vw, 2rem);
    color: var(--primary);
    @media (min-width: 350px) {
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: var(--alt);
    }
  }
}
